/* ===========================HeroSection======================= */

.page {
    position: relative;
    background-color: black;
}

.page::before {
    content: "";
    background-image: url('https://i.postimg.cc/wvMVf9DR/Untitled.jpg');
    /* background-image: url('https://i.postimg.cc/ZKg3KWgg/Designer.jpg'); */

    background-size: cover;
    background-position: center;

    background-repeat: no-repeat;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    background-color: black;
    /* overflow: hidden; */
    filter: blur(2px);
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.content {
    font-family: 'Montserrat', sans-serif;
    padding: 20px;
    width: 40%;

    position: absolute;

    bottom: 8%;
    right: 100px;
    padding: 20px;
    color: white;
}

.content h1 {
    font-size: 8vw;
    font-weight: bolder;
}

.content p {
    font-size: 1.5vw;
}

.gradient-text {
    font-size: 3em;
    background: linear-gradient(to bottom, #884400, #FFFFFF);

    -webkit-background-clip: text;
    background-clip: text;
    /* Clip the text to the background */
    -webkit-text-fill-color: transparent;
    /* Make the text transparent */
    display: inline-block;
    /* Ensure gradient applies properly */
}

.change-text:after {
    content: "NIT ROURKELA STUDENT CHAPTER";
    animation: spin 3s linear infinite;
}

@keyframes spin {
    0% {
        content: "NIT ROURKELA STUDENT CHAPTER";
    }

    50% {
        content: "WE MAKE HUMAN POWERED VEHICLES"
    }
}
 
#tsparticles {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    padding: 0;
    margin: 0;
    z-index: 10;
}

@media (min-width:1000px) and (max-width:1300px) {
