/* ================================ Gallery ================================= */

.gallery-container {
    display: flex;
    align-items: center;
    flex-direction: column;
    content: "";
    /* background-image: url('https://i.postimg.cc/MpHh8FRx/Designer-1.jpg'); */
    background-image: url('https://i.postimg.cc/FzwQRkKV/3392075.jpg');

    background-size: cover;
    background-position: center;

    background-repeat: no-repeat;
    /* height: 150vh; */


    /* background: linear-gradient(to right, whitesmoke, #ccc); */
    padding: 4em 0px;
    background-color: white;


}

.gallery-container h1 {

    padding-bottom: 30px;
}

.grid-container {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    grid-template-rows: repeat(3, auto);
    gap: 10px;

}

.grid-item:hover {
    cursor: pointer;
}

.grid-item {
    height: 300px;
    background-color: #ccc;
}

.grid-item img {
    height: 100%;
    width: 100%;
}

/* Classes to define the widths of specific grid items */
.full-width {
    grid-column: span 12;
    /* Spans across both columns */
}

.large-width {
    grid-column: span 8;
    /* Takes the width of 1 column */
}

.small-width {
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    display: flex;
    align-items: center;
    justify-content: center;
    /* background-color: grey; */
    grid-column: span 4;
    /* Takes the width of 1 column */
    background: linear-gradient(to bottom, rgb(23, 22, 22), #ccc);

    color: white;
}

.small-width:hover {
    cursor: pointer;
    /* background-color: rgb(69, 68, 68); */
    background: linear-gradient(to top, grey, white);

    color: black;

}

.equal-width {
    grid-column: span 6;
    /* Takes the width of 1 column */
}

@media (max-width: 1000px) {
    .grid-container {
        /* height: 60vh; */
        width: 90vw;
    }

    .grid-item {
        height: 30vw;
    }

    .grid-container {
        gap: 2px;
    }

    .grid-container h3 {
        font-size: large;
    }
}

